.wrapper-fluid{
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
  }


/**
 * Clearfix
 */
%clearfix {

    &:after {
        content: "";
        display: table;
        clear: both;
    }
}




/**
 * BUTTON STYLES & UTILS
 */

a.button {
    display: inline-block;
    margin: 0 0 1em;
    padding: 1em 2em;
    border-radius: .3em;
    box-shadow: inset 0 -2px 0 rgba(0,0,0,.2),0 2px 4px rgba(0,0,0,.1);
    font-weight: 500;
    font-size: 1.2em;
    &:hover{
      text-decoration: none;
      box-shadow: inset 0 -2px 0 rgba(0,0,0,.2),0 1px 4px rgba(0,0,0,.2);
    }
}

.button-yellow{
  background-color: #fbd159;
  color: rgba(0,0,0,.65);
  &:hover{
    background-color: #fbca40;
    color: rgba(0,0,0,.7);
  }
}

.button-blue{
  background-color: #103c66;
  color: rgba(245,245,245,0.8);
  font-weight: 200 !important;
  &:hover{
    background-color: #0c3053;
    color: rgba(245,245,245,0.9);
    font-weight: 200 !important;  }
}

.hero a.button-mobile-only{
  display: inline-flex;

  @include bp(md){
    display: none;
  }
}
.hero a.button-mobile-hide{
  display: none;

  @include bp(md){
    display: inline-flex;
  }
}
